/* Global container */
.waterlog-container {
  max-width: 920px;
  margin: 0 auto;
  padding: 1.25rem;
  background: linear-gradient(180deg, #f7f1e3 0%, #fffdf9 100%);
  border-radius: 18px;
  box-shadow: 0 8px 20px rgba(74, 168, 216, 0.15);
  color: #2c3a47;
}
.waterlog-title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin: 1rem 0 1.5rem;
  color: #1d3c5b; /* deep ocean blue */
  border-bottom: 3px solid #4aa8d8; /* ocean blue underline */
  padding-bottom: 0.5rem;
}

/* Weekly blocks */
.weekly-block {
  margin: 1.5rem 0;
  border-left: 5px solid #4aa8d8;
  padding-left: 0.8rem;
}

/* Tables */
.waterlog-table,
.analysis-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 0.75rem;
  margin-left: auto;
  margin-right: auto;
  background: #fffdf9;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.06);
}

.waterlog-table th,
.waterlog-table td,
.analysis-table th,
.analysis-table td {
  padding: 0.75rem 0.9rem;
  border-bottom: 1px solid #f1ebe4;
  text-align: left;
}

.waterlog-table thead tr,
.analysis-table thead tr {
  background: #a3e4db;
  color: #1d3c5b;
  font-weight: 600;
}

.waterlog-table tbody tr:hover {
  background: #f8fafa;
}

/* Controls buttons under title */
.log-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: 1rem 0 1.5rem;
}

/* Make both buttons visually balanced */
.log-controls button {
  min-width: 160px;
  text-align: center;
}

/* Back Buttons */
.back-button {
  background: #4aa8d8;
  color: #fffdf9;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.3s;
}
.back-button:hover {
  background: #3d90bb;
}

/* Switch Unit button */
.unit-toggle-btn {
  background: #4aa8d8;
  color: #fffdf9;
  border: none;
  padding: 0.55rem 1rem;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.3s;
}
.unit-toggle-btn:hover {
  background: #3d90bb;
}

/* Delete button in tables*/
.delete-button {
  background: #f8a488;
  border: none;
  color: #fffdf9;
  padding: 0.4rem 0.7rem;
  border-radius: 10px;
  cursor: pointer;
  font-size: 0.9rem;
}
.delete-button:hover {
  background: #e68666;
}

/* Analysis section */
.analysis-section {
  margin-top: 2rem;
  padding: 1rem;
  background: #fffdf9;
  border: 2px dashed #f7f1e3;
  border-radius: 16px;
}

/* Flow box (pattern) */
.pattern-flow {
  position: relative;
  border: 2px solid #a3e4db;
  background: linear-gradient(135deg, #e7f9f7 0%, #fffdf9 100%);
  border-radius: 18px;
  padding: 1rem;
  margin: 1rem 0 1.5rem;
  box-shadow: 0 6px 14px rgba(74, 168, 216, 0.15);
}

.pattern-flow::after {
  content: "";
  position: absolute;
  top: -10px;
  left: 36px;
  width: 18px;
  height: 18px;
  background: #fffdf9;
  border-left: 2px solid #a3e4db;
  border-top: 2px solid #a3e4db;
  transform: rotate(45deg);
}

.pattern-flow__title {
  display: inline-block;
  font-weight: 700;
  padding: 0.25rem 0.6rem;
  border-radius: 10px;
  background: #4aa8d8;
  color: #fffdf9;
  margin-bottom: 0.5rem;
}

.pattern-flow__content {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
  margin: 0.4rem 0 0.75rem;
}

.pattern-chip {
  font-size: 0.95rem;
  background: #f7f1e3;
  border: 1px solid #e0d7c6;
  color: #1d3c5b;
  padding: 0.4rem 0.7rem;
  border-radius: 999px;
}

/* Responsive */
@media (max-width: 720px) {
  .waterlog-container {
    padding: 1rem;
  }
  .pattern-flow {
    padding: 0.8rem;
  }
  .waterlog-table th,
  .waterlog-table td,
  .analysis-table th,
  .analysis-table td {
    font-size: 0.85rem;
    padding: 0.5rem;
  }
}
