.waterapp-container {
  max-width: 500px;
  margin: 2rem auto;
  padding: 1.5rem;
  background: linear-gradient(180deg, #f7f1e3 0%, #fffdf9 100%);
  border-radius: 18px;
  box-shadow: 0 8px 22px rgba(74, 168, 216, 0.18);
  color: #2c3a47;
  text-align: center;
}

/* Title */
.waterapp-container h2 {
  color: #1d3c5b;
  margin-bottom: 1.2rem;
  font-size: 1.6rem;
  font-weight: 700;
}

/* Inputs */
.form-input {
  width: 100%;
  padding: 0.6rem 0.9rem;
  margin: 0.5rem 0;
  border: 1px solid #e0d7c6;
  border-radius: 12px;
  font-size: 1rem;
  background: #fffdf9;
  color: #2c3a47;
  box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.04);
  transition: border-color 0.3s;
}
.form-input:focus {
  outline: none;
  border-color: #4aa8d8;
  box-shadow: 0 0 0 3px rgba(74, 168, 216, 0.2);
}
/* Remove up/down arrows from number inputs */
.form-input.no-spinner::-webkit-inner-spin-button,
.form-input.no-spinner::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Shorter width for volume input when next to unit switch */
.volume-input {
  flex: 1;
  max-width: 70%;
  margin-right: 0.5rem;
}

/* Buttons */
.form-btn {
  display: inline-block;
  padding: 0.6rem 1.1rem;
  margin: 0.6rem 0.4rem;
  font-size: 1rem;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}
/* switch group container */
.unit-switch-group {
  display: inline-flex;
  margin-left: 0.5rem;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
}

/* Base style for switch buttons */
.unit-btn {
  padding: 0.6rem 1.1rem;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  background: #f7f1e3;
  color: #2c3a47;
  cursor: pointer;
  transition: background 0.3s, color 0.3s;
}

/* Remove spacing between buttons */
.unit-btn + .unit-btn {
  border-left: 1px solid #e0d7c6;
}

/* Active (selected) button */
.unit-btn.active {
  background: #4aa8d8;
  color: #fffdf9;
}

/* Hover effect */
.unit-btn:hover:not(.active) {
  background: #ece5d9;
}

/* Primary (Add Log) */
.form-btn.primary {
  background: #4aa8d8;
  color: #fffdf9;
  border: none;
}
.form-btn.primary:hover {
  background: #3d90bb;
}

/* Secondary (View History) */
.form-btn.secondary {
  background: #f8a488;
  color: #fffdf9;
  border: none;
}
.form-btn.secondary:hover {
  background: #e68666;
}

/* Success Message */
.success-msg {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  background: #a3e4db;
  color: #1d3c5b;
  font-weight: 500;
  display: inline-block;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.08);
}

/* Responsive */
@media (max-width: 480px) {
  .waterapp-container {
    padding: 1rem;
  }
  .form-btn {
    width: 100%;
    margin: 0.5rem 0;
  }
}
